$(function () {
	const preCatalogSlider = new Swiper('.js-pre-catalog-slider', {
		slidesPerView: 1,
		spaceBetween: 16,
		speed: 1200,
		simulateTouch: false,
		// nested: true,
		enabled: false,
		breakpoints: {
			768: {
				slidesPerView: 2,
				enabled: true,
			},
			1280: {
				slidesPerView: 3,
				enabled: true,
			}
		},
	});

	const productThumbs = new Swiper('.js-product-thumbs', {
		// direction: 'vertical',
		slidesPerView: 3,
		spaceBetween: 6,
		watchSlidesProgress: true,
		breakpoints: {
			768: {
				direction: 'vertical',
				spaceBetween: 10,
			}
		},
	});

	const productSlider = new Swiper('.js-product-slider', {
		slidesPerView: 1,
		speed: 1200,
		thumbs: {
			swiper: productThumbs,
		},
	});

	const blogDetailSlider = new Swiper('.js-blog-detail-slider', {
		slidesPerView: 1,
		spaceBetween: 4,
		speed: 1200,
		// mousewheel: {
		// 	enabled: true,
		// 	releaseOnEdges: true,
		// },
		// nested: true,

		simulateTouch: false,
		// touchStartForcePreventDefault: true,
		// threshold: '0',
		// touchStartPreventDefault: false,
		// touchMoveStopPropagation: true,
		// preventClicks: false,
		// preventClicksPropagation: false,
		// preventInteractionOnTransition: true,
		breakpoints: {
			768: {
				// slidesPerView: 2.06,
				slidesPerView: 2,
				spaceBetween: 12,
			}
			// 1280: {
			// 	// slidesPerView: 1.89,
			// 	// slidesPerView: 2,
			// 	spaceBetween: 12,
			// }
		},
	});
})